/* ======================
   原神风格主页样式
   ====================== */

/* 重置样式 */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}



/* 动画元素GPU加速 - 优化版 */
.character-card:hover,
.research-card:hover,
.timeline-item:hover,
.contact-card:hover {
    will-change: transform;
}

.character-card,
.research-card,
.timeline-item,
.contact-card {
    will-change: auto;
    transition: transform 0.2s ease;
}

/* 关键UI元素优化 */
.genshin-nav,
.hero-bg-overlay {
    transform: translateZ(0);
    contain: layout;
}

/* 减少重绘的优化 */
.character-img,
.element-symbol {
    contain: layout style;
}

/* 移动端优化 */
html {
    -webkit-text-size-adjust: 100%;
    -webkit-tap-highlight-color: transparent;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    text-rendering: optimizeSpeed;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    scroll-behavior: smooth;
}

/* 安全区域适配 */
body {
    padding-left: env(safe-area-inset-left);
    padding-right: env(safe-area-inset-right);
}

.genshin-nav {
    padding-top: env(safe-area-inset-top);
}

.genshin-footer {
    padding-bottom: env(safe-area-inset-bottom);
}

/* 禁用iOS的橡皮筋效果 */
body {
    overscroll-behavior: none;
    -webkit-overflow-scrolling: touch;
}

/* 防止移动端缩放导致的布局问题 */
input, textarea, select {
    font-size: 16px;
}

/* 确保可点击元素有足够的触摸区域 */
.nav-link, .element-btn, .lang-btn, .contact-card, .traveler-profile-btn {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    user-select: none;
}

/* 原神风格加载动画 */
.genshin-loader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #ffffff;
    z-index: 10000;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    opacity: 1;
    transition: opacity 0.5s ease-out;
}

.genshin-loader.fade-out {
    opacity: 0;
    pointer-events: none;
}

.loader-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #ffffff;
    overflow: hidden;
}

.loader-stars {
    position: absolute;
    width: 100%;
    height: 100%;
}

.loader-stars .star {
    position: absolute;
    width: 4px;
    height: 4px;
    background: #d4af37;
    border-radius: 50%;
    animation: starTwinkle 2s ease-in-out infinite;
}

.loader-stars .star:nth-child(1) { top: 20%; left: 15%; animation-delay: 0s; }
.loader-stars .star:nth-child(2) { top: 30%; left: 80%; animation-delay: 0.5s; }
.loader-stars .star:nth-child(3) { top: 60%; left: 25%; animation-delay: 1s; }
.loader-stars .star:nth-child(4) { top: 70%; left: 85%; animation-delay: 1.5s; }
.loader-stars .star:nth-child(5) { top: 40%; left: 60%; animation-delay: 0.7s; }
.loader-stars .star:nth-child(6) { top: 80%; left: 40%; animation-delay: 1.2s; }
.loader-stars .star:nth-child(7) { top: 15%; left: 70%; animation-delay: 0.3s; }
.loader-stars .star:nth-child(8) { top: 50%; left: 10%; animation-delay: 0.8s; }

@keyframes starTwinkle {
    0%, 100% { opacity: 0.3; transform: scale(1); }
    50% { opacity: 1; transform: scale(1.5); }
}

.loader-content {
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    text-align: center;
}



.loader-text {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.loader-logo {
    margin-bottom: 3rem;
    animation: logoFadeIn 1s ease-out 0.5s both;
}

.genshin-logo {
    max-width: 350px;
    height: auto;
    filter: drop-shadow(0 6px 12px rgba(212, 175, 55, 0.4));
}

@keyframes logoFadeIn {
    from { opacity: 0; transform: translateY(30px); }
    to { opacity: 1; transform: translateY(0); }
}

.loader-progress {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1.5rem;
}

.progress-bar-loader {
    width: 300px;
    height: 8px;
    background: #f0f0f0;
    border-radius: 4px;
    margin: 0 auto 1rem;
    overflow: hidden;
    border: 2px solid #d4af37;
}

.progress-fill-loader {
    height: 100%;
    background: linear-gradient(90deg, #d4af37, #f4e071, #d4af37);
    background-size: 200% 100%;
    width: 100%;
    animation: progressLoad 3s ease-in-out;
    border-radius: 2px;
}

@keyframes progressLoad {
    0% { 
        width: 0%; 
        background-position: 0% 50%;
    }
    100% { 
        width: 100%; 
        background-position: 200% 50%;
    }
}

.loading-dots {
    color: #8b7355;
    font-size: 1.1rem;
    font-weight: 500;
}

.dot {
    animation: dotBounce 1.4s ease-in-out infinite;
}

.dot-2 { animation-delay: 0.2s; }
.dot-3 { animation-delay: 0.4s; }
.dot-4 { animation-delay: 0.6s; }

@keyframes dotBounce {
    0%, 80%, 100% { opacity: 0.3; }
    40% { opacity: 1; }
}

/* 元素切换动画 */
@keyframes elementSwitchRotate {
    0% {
        transform: scale(1) rotate(0deg);
        filter: brightness(1) saturate(1);
        box-shadow: 0 0 20px var(--glow-color);
    }
    20% {
        transform: scale(1.15) rotate(72deg);
        filter: brightness(1.2) saturate(1.3);
        box-shadow: 0 0 25px var(--glow-color);
    }
    40% {
        transform: scale(1.25) rotate(144deg);
        filter: brightness(1.4) saturate(1.5);
        box-shadow: 0 0 30px var(--glow-color), 0 0 40px var(--glow-color);
    }
    60% {
        transform: scale(1.3) rotate(216deg);
        filter: brightness(1.6) saturate(1.7);
        box-shadow: 0 0 35px var(--glow-color), 0 0 50px var(--glow-color);
    }
    80% {
        transform: scale(1.15) rotate(288deg);
        filter: brightness(1.3) saturate(1.4);
        box-shadow: 0 0 25px var(--glow-color);
    }
    100% {
        transform: scale(1) rotate(360deg);
        filter: brightness(1) saturate(1);
        box-shadow: 0 0 20px var(--glow-color);
    }
}

/* 元素切换淡入动画 */
@keyframes elementFadeIn {
    0% {
        opacity: 0;
        transform: scale(0.8);
        filter: blur(2px);
    }
    50% {
        opacity: 0.8;
        transform: scale(1.1);
        filter: blur(1px);
    }
    100% {
        opacity: 1;
        transform: scale(1);
        filter: blur(0px);
    }
}

/* 欢迎脉冲动画 */
@keyframes welcomePulse {
    0% {
        transform: scale(1);
        box-shadow: 0 0 20px var(--glow-color);
    }
    50% {
        transform: scale(1.1);
        box-shadow: 0 0 30px var(--glow-color), 0 0 40px var(--glow-color);
    }
    100% {
        transform: scale(1);
        box-shadow: 0 0 20px var(--glow-color);
    }
}

/* 移动端响应式 */
@media (max-width: 768px) {
    .genshin-logo {
        max-width: 280px;
    }
    
    .progress-bar-loader {
        width: 250px;
    }
    
    .loader-logo {
        margin-bottom: 2rem;
    }
}

@media (max-width: 480px) {
    .genshin-logo {
        max-width: 220px;
    }
    
    .progress-bar-loader {
        width: 200px;
    }
}

/* 原有样式 */

:root {
    /* 元素颜色系统 */
    --anemo-primary: #74C2C2;
    --anemo-secondary: #4FD0C7;
    --anemo-glow: rgba(116, 194, 194, 0.6);
    
    --geo-primary: #F7E781;
    --geo-secondary: #D2B04C;
    --geo-glow: rgba(247, 231, 129, 0.6);
    
    --electro-primary: #AD6CE0;
    --electro-secondary: #A661D8;
    --electro-glow: rgba(173, 108, 224, 0.6);
    
    --pyro-primary: #EC4923;
    --pyro-secondary: #FF6666;
    --pyro-glow: rgba(236, 73, 35, 0.6);
    
    --cryo-primary: #9FD3E0;
    --cryo-secondary: #77B9D0;
    --cryo-glow: rgba(159, 211, 224, 0.6);
    
    --hydro-primary: #00BFFF;
    --hydro-secondary: #1E90FF;
    --hydro-glow: rgba(0, 191, 255, 0.6);
    
    --dendro-primary: #A5C83B;
    --dendro-secondary: #8FB92B;
    --dendro-glow: rgba(165, 200, 59, 0.6);
    
    /* 当前活动元素的动态颜色 */
    --primary-color: var(--anemo-primary);
    --secondary-color: var(--anemo-secondary);
    --glow-color: var(--anemo-glow);
    
    /* 基础色彩 */
    --bg-primary: #0a0e1a;
    --bg-secondary: #1a1f2e;
    --bg-card: rgba(26, 31, 46, 0.85); /* 稍微增加透明度 */
    --text-primary: #ffffff;
    --text-secondary: #c9aa71;
    --text-muted: #8892b0;
    --accent-gold: #d4af37;
    --border-color: rgba(255, 255, 255, 0.1);
    
    /* 字体 */
    --font-main: 'Poppins', 'Noto Sans SC', sans-serif;
    --font-title: 'Cinzel', 'Noto Sans SC', serif;
    
    /* 动画时长 - 性能优化 */
    --transition-fast: 0.15s;
    --transition-normal: 0.2s;
    --transition-slow: 0.3s;
}

/* 全局样式 */
html {
    scroll-behavior: smooth;
}

body {
    font-family: var(--font-main);
    background: var(--bg-primary);
    color: var(--text-primary);
    line-height: 1.6;
    overflow-x: hidden;
    /* iOS Safari 触摸优化 */
    -webkit-tap-highlight-color: transparent;
    -webkit-touch-callout: none;
    overscroll-behavior: none;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

/* 星空背景动画 */
.stars-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background: linear-gradient(
        180deg,
        #0a0e1a 0%,
        #1a1f2e 50%,
        #2a2f3e 100%
    );
    /* 添加硬件加速 */
    transform: translateZ(0);
    will-change: transform;
    backface-visibility: hidden;
    perspective: 1000px;
}

.stars,
.stars2,
.stars3 {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    /* 简化星空背景，使用纯粹的点状背景替代复杂渐变 */
    background-image: 
        radial-gradient(1px 1px at 20px 30px, rgba(255,255,255,0.8), transparent),
        radial-gradient(1px 1px at 90px 40px, rgba(255,255,255,0.6), transparent);
    background-repeat: repeat;
    background-size: 300px 200px;
    /* 硬件加速 */
    transform: translateZ(0);
    will-change: transform;
    contain: content;
    pointer-events: none;
}

.stars {
    /* 移除动画 */
    opacity: 0.8;
}

.stars2 {
    background-size: 250px 120px;
    opacity: 0.5;
    /* 移除动画 */
}

.stars3 {
    background-size: 300px 150px;
    opacity: 0.3;
    /* 移除动画 */
}





/* 导航栏 */
.genshin-nav {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    background: rgba(10, 14, 26, 0.9);
    backdrop-filter: blur(10px);
    border-bottom: 1px solid var(--border-color);
    transition: all var(--transition-normal);
}

.nav-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem 1.5rem;
    max-width: 1400px;
    margin: 0 auto;
}

.nav-logo {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    font-family: var(--font-title);
    font-weight: 600;
    font-size: 1.2rem;
}

.logo-icon {
    color: var(--primary-color);
    font-size: 1.5rem;
}

.logo-text {
    color: var(--primary-color);
    transition: color 0.3s ease;
}

/* 移动端汉堡菜单 */
.nav-mobile-toggle {
    display: none;
    flex-direction: column;
    cursor: pointer;
    padding: 0.5rem;
    gap: 0.3rem;
    z-index: 1001;
}

.nav-mobile-toggle span {
    width: 25px;
    height: 3px;
    background-color: var(--primary-color);
    transition: all 0.3s ease;
    border-radius: 2px;
}

.nav-mobile-toggle.active span:nth-child(1) {
    transform: rotate(45deg) translate(6px, 6px);
}

.nav-mobile-toggle.active span:nth-child(2) {
    opacity: 0;
}

.nav-mobile-toggle.active span:nth-child(3) {
    transform: rotate(-45deg) translate(6px, -6px);
}



.nav-menu {
    display: flex;
    gap: 1.5rem;
}

.nav-link {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.3rem;
    text-decoration: none;
    color: var(--text-muted);
    padding: 0.5rem 0.8rem;
    border-radius: 8px;
    transition: all var(--transition-normal);
    position: relative;
    overflow: hidden;
    min-width: 70px;
    font-size: 0.9rem;
}

.nav-link:hover,
.nav-link.active {
    color: var(--primary-color);
    transform: translateY(-2px);
}

.element-toggle {
    position: relative;
    display: flex;
    align-items: center;
}

.elements-toggle-btn {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.5rem 1rem;
    background: var(--bg-card);
    border: none;
    border-radius: 8px;
    color: var(--text-muted);
    cursor: pointer;
    transition: all var(--transition-normal);
    font-size: 0.9rem;
}

.elements-toggle-btn:hover {
    color: var(--primary-color);
    background: var(--border-color);
}

.toggle-arrow {
    transition: transform var(--transition-normal);
}

.elements-toggle-btn.active .toggle-arrow {
    transform: rotate(180deg);
}

.elements-dropdown {
    position: absolute;
    top: 100%;
    right: 0;
    background: var(--bg-card);
    border: 1px solid var(--border-color);
    border-radius: 12px;
    padding: 0.5rem;
    display: flex;
    gap: 0.5rem;
    opacity: 0;
    visibility: hidden;
    transform: translateY(-10px);
    transition: all var(--transition-normal);
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
    backdrop-filter: blur(20px);
    z-index: 1000;
    margin-top: 0.5rem;
}

.elements-dropdown.show {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
}

.element-btn {
    width: 40px;
    height: 40px;
    border: none;
    border-radius: 50%;
    background: var(--bg-card);
    color: var(--text-muted);
    cursor: pointer;
    transition: all var(--transition-normal);
    display: flex;
    align-items: center;
    justify-content: center;
}

.element-btn:hover,
.element-btn.active {
    transform: scale(1.1);
    box-shadow: 0 0 20px rgba(255, 255, 255, 0.3);
}

.element-btn[data-element="anemo"].active { 
    background: var(--anemo-primary); 
    box-shadow: 0 0 20px var(--anemo-glow);
}
.element-btn[data-element="geo"].active { 
    background: var(--geo-primary); 
    box-shadow: 0 0 20px var(--geo-glow);
}
.element-btn[data-element="electro"].active { 
    background: var(--electro-primary); 
    box-shadow: 0 0 20px var(--electro-glow);
}
.element-btn[data-element="pyro"].active { 
    background: var(--pyro-primary); 
    box-shadow: 0 0 20px var(--pyro-glow);
}
.element-btn[data-element="cryo"].active { 
    background: var(--cryo-primary); 
    box-shadow: 0 0 20px var(--cryo-glow);
}
.element-btn[data-element="hydro"].active { 
    background: var(--hydro-primary); 
    box-shadow: 0 0 20px var(--hydro-glow);
}
.element-btn[data-element="dendro"].active { 
    background: var(--dendro-primary); 
    box-shadow: 0 0 20px var(--dendro-glow);
}

.language-switch {
    display: flex;
    background: var(--bg-card);
    border-radius: 20px;
    overflow: hidden;
}

.lang-btn {
    padding: 0.5rem 1rem;
    border: none;
    background: transparent;
    color: var(--text-muted);
    cursor: pointer;
    transition: all var(--transition-normal);
}

.lang-btn.active {
    background: var(--primary-color);
    color: var(--bg-primary);
}

/* 主角介绍区域 */
.hero-section {
    min-height: 100vh;
    display: flex;
    align-items: center;
    position: relative;
    padding: 100px 0;
    z-index: 1;
}

.hero-bg-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(
        135deg,
        rgba(116, 194, 194, 0.1) 0%,
        rgba(10, 14, 26, 0.8) 50%,
        rgba(173, 108, 224, 0.1) 100%
    );
    z-index: -1;
}

.hero-content {
    display: grid;
    grid-template-columns: 1fr 400px;
    gap: 3rem;
    align-items: stretch;
    max-width: 1200px;
    margin: 0 auto;
    width: 100%;
}

.character-card {
    background: var(--bg-card);
    border-radius: 20px;
    padding: 2rem;
    position: relative;
    backdrop-filter: blur(20px);
    border: 1px solid var(--border-color);
    overflow: hidden;
    transition: all var(--transition-normal);
    height: 100%;
    display: flex;
    flex-direction: column;
}

.character-language-switch {
    position: absolute;
    top: 1rem;
    left: 1rem;
    display: flex;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 15px;
    overflow: hidden;
    backdrop-filter: blur(10px);
    border: 1px solid var(--border-color);
    z-index: 10;
}

.char-lang-btn {
    padding: 0.4rem 0.8rem;
    border: none;
    background: transparent;
    color: var(--text-muted);
    cursor: pointer;
    transition: all var(--transition-normal);
    font-size: 0.8rem;
    font-weight: 500;
}

.char-lang-btn.active {
    background: var(--primary-color);
    color: var(--bg-primary);
}

.char-lang-btn:hover:not(.active) {
    color: var(--primary-color);
    background: rgba(255, 255, 255, 0.1);
}

.character-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(
        45deg,
        transparent 0%,
        rgba(116, 194, 194, 0.1) 50%,
        transparent 100%
    );
    z-index: -1;
}

.character-card:hover {
    transform: translateY(-3px) translateZ(0);
    box-shadow: 0 15px 30px rgba(116, 194, 194, 0.25);
}

.character-avatar {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 2rem;
    position: relative;
}

.avatar-frame {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    background: linear-gradient(45deg, var(--anemo-primary), var(--anemo-secondary));
    padding: 5px;
    position: relative;
    margin-bottom: 1rem;
}

/* 旅行者档案按钮 */
.traveler-profile-btn {
    position: absolute;
    bottom: 15px;
    right: 15px;
    width: 45px;
    height: 45px;
    background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-size: 1.2rem;
    text-decoration: none;
    box-shadow: 0 4px 15px var(--glow-color);
    transition: all 0.3s ease;
    z-index: 5;
    /* 静态档案按钮，不再脉动 */
    border: 2px solid rgba(255, 255, 255, 0.3);
}

.traveler-profile-btn:hover {
    transform: scale(1.15) rotate(15deg);
    box-shadow: 0 8px 30px var(--glow-color);
    animation-play-state: paused;
    border-color: rgba(255, 255, 255, 0.6);
}

.traveler-profile-btn:active {
    transform: scale(0.95);
}

/* 移除脉冲动画 - 静态设计 */

.character-img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
    transition: transform 0.5s ease;
    cursor: pointer;
}

.avatar-glow {
    position: absolute;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    border-radius: 50%;
    background: radial-gradient(circle, var(--anemo-glow) 0%, transparent 70%);
    opacity: 0.6;
    z-index: -1;
}

.element-symbol {
    position: absolute;
    top: -10px;
    right: -10px;
    width: 50px;
    height: 50px;
    background: var(--primary-color);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--bg-primary);
    font-size: 1.2rem;
    box-shadow: 0 0 20px var(--glow-color);
    transition: transform 0.25s cubic-bezier(0.34, 1.56, 0.64, 1), 
                filter 0.25s ease,
                box-shadow 0.25s ease;
    will-change: transform, filter, box-shadow;
    transform-origin: center;
    backface-visibility: hidden;
    /* 确保硬件加速 */
    transform: translateZ(0);
    /* 防止iOS Safari触摸穿透 */
    touch-action: manipulation;
    -webkit-tap-highlight-color: transparent;
    -webkit-touch-callout: none;
    user-select: none;
}

.element-symbol.switching {
    pointer-events: none;
}

.element-symbol i {
    transition: transform 0.2s ease;
    will-change: transform;
    backface-visibility: hidden;
}

/* 触摸设备优化 */
@media (pointer: coarse) {
    .element-symbol {
        /* 增大触摸区域 */
        width: 55px;
        height: 55px;
        top: -12px;
        right: -12px;
    }
    
    .element-symbol:active {
        transform: scale(0.95) translateZ(0);
    }
}

/* 高刷新率屏幕优化 */
@media (min-resolution: 120dpi) {
    .element-symbol {
        /* 确保在高DPI屏幕上的动画流畅性 */
        transform: translate3d(0, 0, 0);
    }
}

/* 减少动画偏好设置 */
@media (prefers-reduced-motion: reduce) {
    .element-symbol {
        transition: none;
    }
    
    @keyframes elementSwitchRotate {
        0%, 100% {
            transform: scale(1) rotate(0deg);
            filter: brightness(1) saturate(1);
            box-shadow: 0 0 20px var(--glow-color);
        }
        50% {
            transform: scale(1.1) rotate(0deg);
            filter: brightness(1.2) saturate(1.2);
            box-shadow: 0 0 25px var(--glow-color);
        }
    }
    
    @keyframes elementFadeIn {
        0%, 100% {
            opacity: 1;
            transform: scale(1);
            filter: blur(0px);
        }
    }
}

.element-symbol.anemo { background: var(--anemo-primary); box-shadow: 0 0 20px var(--anemo-glow); }
