/* 横向时间轴线条 - 在底部连接时间标签 */
.timeline-container::before {
    content: '';
    position: absolute;
    left: 2rem;
    right: 2rem;
    bottom: 0.5rem;
    height: 4px;
    background: linear-gradient(
        90deg,
