    z-index: 10;
    white-space: nowrap;
    letter-spacing: 0.5px;
}

/* 连接线从时间标签到时间轴 */
.timeline-date::after {
    content: '';
    position: absolute;
    bottom: -1.5rem;
    left: 50%;
    transform: translateX(-50%);
    width: 3px;
    height: 1.5rem;
    background: linear-gradient(180deg, var(--primary-color), var(--secondary-color));
