    background: var(--primary-color);
    color: var(--bg-primary);
    text-decoration: none;
    border-radius: 20px;
    font-size: 0.9rem;
    transition: transform var(--transition-normal), background var(--transition-normal);
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    /* 启用硬件加速 */
    will-change: transform;
    transform: translateZ(0);
}

.pub-btn:hover {
    background: var(--secondary-color);
    transform: translateY(-1px) translateZ(0);
    box-shadow: 0 3px 12px rgba(0, 0, 0, 0.15);
}

/* 教育经历 */
.education-section {
    padding: 100px 0;
    background: linear-gradient(
        135deg,
        rgba(26, 31, 46, 0.5) 0%,
        rgba(10, 14, 26, 0.8) 100%
    );
    position: relative;
    z-index: 1;
}

.education-timeline {
    max-width: 600px;
    margin: 0 auto;
}

.education-item {
    display: flex;
    align-items: center;
    gap: 2rem;
    background: var(--bg-card);
    padding: 2rem;
    border-radius: 20px;
    border: 1px solid var(--border-color);
    margin-bottom: 2rem;
    transition: all var(--transition-normal);
}

.education-item:hover {
    transform: translateX(10px);
    box-shadow: 0 10px 30px var(--glow-color);
    border-color: var(--primary-color);
}

.edu-icon {
    width: 60px;
    height: 60px;
    background: var(--primary-color);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--bg-primary);
    font-size: 1.5rem;
    flex-shrink: 0;
    box-shadow: 0 0 20px var(--glow-color);
    transition: all 0.3s ease;
}

.edu-content h3 {
    margin-bottom: 0.5rem;
    color: var(--text-primary);
    transition: color 0.3s ease;
}

.education-item:hover .edu-content h3 {
    color: var(--primary-color);
}

.edu-school {
    color: var(--primary-color);
    margin-bottom: 0.5rem;
    transition: color 0.3s ease;
}

.edu-date {
    color: var(--text-muted);
    font-size: 0.9rem;
    margin-bottom: 0.5rem;
}

.edu-status {
    display: inline-block;
    padding: 0.3rem 0.8rem;
    border-radius: 15px;
    font-size: 0.8rem;
    font-weight: 600;
    transition: all 0.3s ease;
}

.edu-status.active {
    background: var(--primary-color);
    color: var(--bg-primary);
    box-shadow: 0 0 10px var(--glow-color);
}

.edu-status.completed {
    background: var(--secondary-color);
    color: var(--bg-primary);
    box-shadow: 0 0 10px var(--glow-color);
}

.edu-honor {
    color: var(--accent-gold);
    font-size: 0.9rem;
    margin-top: 0.5rem;
}

/* 实习经历 */
.internship-section {
    padding: 100px 0;
    position: relative;
    z-index: 1;
}

.domains-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 2rem;
}

.domain-card {
    background: var(--bg-card);
    border-radius: 20px;
    padding: 2rem;
    border: 1px solid var(--border-color);
    transition: all var(--transition-normal);
    position: relative;
    overflow: hidden;
}

.domain-card:hover {
    transform: translateY(-10px);
}

.domain-card:hover {
    box-shadow: 0 20px 40px var(--glow-color);
    border-color: var(--primary-color);
}

.domain-header h3 {
    font-size: 1.3rem;
    margin-bottom: 0.5rem;
    color: var(--primary-color);
    transition: color 0.3s ease;
}

.domain-period {
    color: var(--text-secondary);
    font-size: 0.9rem;
    margin-bottom: 1rem;
}

.domain-location {
    display: flex;
    align-items: center;
        gap: 0.5rem;
    color: var(--text-muted);
}

.domain-glow {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-radius: 20px;
    opacity: 0;
    transition: opacity var(--transition-normal);
    z-index: -1;
    box-shadow: inset 0 0 50px var(--glow-color);
}

.domain-card:hover .domain-glow {
    opacity: 1;
}

/* 访客统计 */
.visitor-section {
    padding: 100px 0;
    background: linear-gradient(
        135deg,
        rgba(26, 31, 46, 0.5) 0%,
        rgba(10, 14, 26, 0.8) 100%
    );
    position: relative;
    z-index: 1;
}

.map-container {
    text-align: center;
    background: var(--bg-card);
    border-radius: 20px;
    padding: 2rem;
    border: 1px solid var(--border-color);
}

/* 页脚 */
.genshin-footer {
    background: var(--bg-secondary);
    padding: 2rem 0;
    border-top: 1px solid var(--border-color);
    position: relative;
    z-index: 1;
}

.footer-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    gap: 1rem;
}

.footer-links {
    display: flex;
    gap: 1rem;
}

.footer-links a {
    color: var(--text-muted);
    font-size: 1.2rem;
    transition: color var(--transition-normal);
}

.footer-links a:hover {
    color: var(--primary-color);
}



