/* 深色主题切换 */
[data-theme="dark"] {
    --bg-primary: #0a0e1a;
    --bg-secondary: #1a1f2e;
    --text-primary: #ffffff;
}

[data-theme="light"] {
    --bg-primary: #f8fafc;
    --bg-secondary: #e2e8f0;
    --text-primary: #1a202c;
}

/* 原神技能点风格面板 */
.talent-panel {
    background: var(--bg-card);
    border-radius: 20px;
    padding: 2rem;
    border: 1px solid var(--border-color);
    backdrop-filter: blur(20px);
    position: relative;
    overflow: hidden;
    transition: all 0.3s ease;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.talent-panel::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(
        135deg,
        var(--glow-color) 0%,
        transparent 50%,
        var(--glow-color) 100%
    );
    opacity: 0.1;
    z-index: -1;
}

.talent-header {
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-bottom: 2rem;
    padding-bottom: 1rem;
    border-bottom: 2px solid var(--primary-color);
}

.talent-header .talent-icon {
    width: 50px;
    height: 50px;
    background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-size: 1.5rem;
    box-shadow: 0 0 20px var(--glow-color);
}

.talent-title {
    font-family: var(--font-title);
    font-size: 1.5rem;
    color: var(--primary-color);
    margin: 0;
    transition: color 0.3s ease;
}

.talent-tree {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    flex: 1;
}

.talent-node {
    position: relative;
    background: var(--bg-secondary);
    border-radius: 15px;
    padding: 1rem;
    border: 2px solid var(--border-color);
    transition: all 0.3s ease;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 1rem;
    /* iOS Safari 触摸优化 */
    touch-action: manipulation;
    -webkit-tap-highlight-color: transparent;
    -webkit-touch-callout: none;
    user-select: none;
}

.talent-node.active {
    border-color: var(--primary-color);
    box-shadow: 0 0 20px var(--glow-color);
}

.talent-node:hover {
    transform: translateX(5px);
    box-shadow: 0 5px 25px var(--glow-color);
}

.talent-bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(45deg, transparent, var(--glow-color), transparent);
    opacity: 0;
    transition: opacity 0.3s ease;
    border-radius: 13px;
}

.talent-node:hover .talent-bg {
    opacity: 0.1;
}

.talent-icon-wrapper {
    width: 60px;
    height: 60px;
    background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
    border-radius: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-size: 1.5rem;
    box-shadow: 0 4px 15px var(--glow-color);
    transition: all 0.3s ease;
}

.talent-node:hover .talent-icon-wrapper {
    transform: scale(1.1);
    box-shadow: 0 6px 25px var(--glow-color);
}

.talent-info {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 0.3rem;
}

.talent-name {
    font-weight: 600;
    color: var(--text-primary);
    font-size: 1.1rem;
}



.talent-count {
    font-size: 1.5rem;
    font-weight: bold;
    color: var(--primary-color);
    margin-left: auto;
    background: linear-gradient(45deg, var(--primary-color), var(--secondary-color));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.talent-glow {
    position: absolute;
    top: -2px;
    left: -2px;
    right: -2px;
    bottom: -2px;
    background: linear-gradient(45deg, var(--primary-color), var(--secondary-color));
    border-radius: 17px;
    opacity: 0;
    z-index: -1;
    transition: opacity 0.3s ease;
}

.talent-node.active .talent-glow {
    opacity: 0.3;
}

/* 技能节点链接样式 */
.talent-link {
    text-decoration: none;
    color: inherit;
    display: flex;
    align-items: center;
    gap: 1rem;
}

.talent-link:hover {
    color: inherit;
    text-decoration: none;
}

/* Awards节点特殊样式 */
.talent-awards {
    cursor: pointer;
    /* 确保点击不会触发任何滚动 */
    scroll-behavior: auto !important;
}

.talent-awards:focus {
    outline: 2px solid var(--primary-color);
    outline-offset: 2px;
}

/* 奖项抽屉样式 */
.awards-drawer {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    pointer-events: none;
    opacity: 0;
    transition: opacity var(--drawer-animation-duration, 0.3s) ease;
    --drawer-animation-duration: 0.3s;
    /* 确保抽屉不会影响页面滚动 */
    scroll-behavior: auto !important;
}

.awards-drawer.show {
    pointer-events: auto;
    opacity: 1;
}

.drawer-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(5px);
    /* iOS Safari 触摸优化 */
    touch-action: manipulation;
    -webkit-tap-highlight-color: transparent;
}

.drawer-content {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: var(--bg-card);
    border-radius: 20px 20px 0 0;
    border: 1px solid var(--border-color);
    box-shadow: 0 -10px 30px rgba(0, 0, 0, 0.3);
    transform: translateY(100%);
    transition: transform var(--drawer-animation-duration, 0.3s) ease;
    max-height: 60vh;
    overflow: hidden;
    /* iOS Safari 触摸优化 */
    touch-action: pan-y;
    -webkit-overflow-scrolling: touch;
    -webkit-tap-highlight-color: transparent;
}

.awards-drawer.show .drawer-content {
    transform: translateY(0);
}

.drawer-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.5rem 2rem;
    border-bottom: 1px solid var(--border-color);
    background: linear-gradient(135deg, var(--bg-card), var(--bg-secondary));
}

.drawer-title {
    display: flex;
    align-items: center;
    gap: 0.8rem;
    font-size: 1.3rem;
    font-weight: 600;
    color: var(--primary-color);
}

.drawer-title i {
    font-size: 1.5rem;
    color: var(--primary-color);
}

.drawer-close {
    background: none;
    border: none;
    color: var(--text-muted);
    font-size: 1.2rem;
    cursor: pointer;
    padding: 0.5rem;
    border-radius: 50%;
    transition: all 0.3s ease;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.drawer-close:hover {
    background: var(--border-color);
    color: var(--primary-color);
}

.drawer-body {
    padding: 2rem;
    overflow-y: auto;
    max-height: calc(60vh - 100px);
    /* iOS Safari 触摸优化 */
    -webkit-overflow-scrolling: touch;
    touch-action: pan-y;
    overscroll-behavior: contain;
}

.award-item {
    display: flex;
    gap: 1.5rem;
    padding: 1.5rem;
    background: var(--bg-secondary);
    border-radius: 15px;
    border: 1px solid var(--border-color);
    transition: all 0.3s ease;
    position: relative;
    overflow: hidden;
}

.award-item::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(45deg, transparent, var(--glow-color), transparent);
    opacity: 0;
    transition: opacity 0.3s ease;
}

.award-item:hover {
    transform: translateY(-2px);
    box-shadow: 0 10px 25px var(--glow-color);
    border-color: var(--primary-color);
}

.award-item:hover::before {
    opacity: 0.1;
}

.award-icon {
    flex-shrink: 0;
    width: 60px;
    height: 60px;
    background: linear-gradient(135deg, #FFD700, #FFA500);
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.8rem;
    color: #fff;
    box-shadow: 0 4px 15px rgba(255, 215, 0, 0.3);
}

.award-info {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}

.award-title {
    font-size: 1.2rem;
    font-weight: 600;
    color: var(--text-primary);
    margin: 0;
}

.award-org {
    font-size: 1rem;
    color: var(--primary-color);
    font-weight: 500;
    margin: 0;
}

.award-amount {
    font-size: 1.1rem;
    font-weight: bold;
    color: #FFD700;
    margin: 0;
}

.award-desc {
    font-size: 0.9rem;
    color: var(--text-secondary);
    margin: 0;
    line-height: 1.4;
}

.award-date {
    font-size: 0.85rem;
    color: var(--text-muted);
    align-self: flex-end;
    background: var(--border-color);
    padding: 0.3rem 0.8rem;
    border-radius: 15px;
    margin-top: 0.5rem;
}

